<template>
    <TransitionGroup name="list" tag="ul" class="list-group list-group-hover">
    <li v-for="(todo) in props.todos" :key="todo.id" :class="{disabled: !props.visible}" class="list-group-item d-flex align-items-center p-0 m-0">
        <div class="ps-3 flex-fill align-items-center">
            <input :id="'todo-' + todo.id" type="checkbox" class="form-check-input me-2 rounded-circle"
                v-model="todo.done" @change="handleChange"/>
            <label :for="'todo-' + todo.id" :class="['form-check-label stretched-link fs-6', { completed: todo.done }]">
                {{ todo.name }}
            </label>
        </div>
        <div class="btn-div align-items-center pe-1">
            <button data-bs-toggle="modal" data-bs-target="#editTodoModal" class="btn" @click="() => props.setCurrentTodo(todo)"><i class="bi bi-pencil"></i></button>
        </div>
    </li>
    </TransitionGroup>
</template>

<script setup>
import { defineProps } from 'vue';
const props = defineProps({
    todos: {
        type: Array,
        required: true
    },
    setCurrentTodo: {
        type: Function,
        required: true
    },
    saveTodos: {
        type: Function,
        required: true
    },
    visible: {
        type:Boolean,
        default: true
    }
})

const handleChange = () => {
    setTimeout(() => {
        props.saveTodos();
    }, 500);
}

</script>

<style scoped>
.disabled {
    user-select: none;
}
.btn-div {
    z-index: 1;
}
.completed {
    text-decoration: line-through;
}
.list-enter-active,
.list-leave-active {
  transition: all 0.5s ease;
}
.list-enter-from {
    opacity: 0;
}
.list-enter-to {
    opacity: 1;
}
.list-leave-to {
  opacity: 0;
}
li {
    height: 3.7rem;
    background-color: var(--bs-dark-bg-subtle);
    border-color: var(--bs-dark-bg-subtle);
    border-bottom: 1px solid var(--bs-dark);
}
li, li * {
    cursor: pointer;
}
</style>
