<template>
    <div class="container">
        <TodoItem
            :todos="incompleteTodos"
            :set-current-todo="setCurrentTodo"
            :save-todos="saveTodos"
            :visible="true"
        />
        <button id="showIncompleteButton" ref="showIncompleteButton" 
            class="btn w-100 text-start text-muted"
            :class="{'mt-2':incompleteTodos.length>0}"
            @click="toggleShowCompleted($event)">
            <i :class="chevronClass"></i>
            Completed {{ completedCount }}
        </button>
        <hr v-if="showCompleted" class="m-0 mb-2 p-0 text-black">
        <Transition>
            <TodoItem
                v-show="showCompleted"
                :visible="showCompleted"
                :todos="completedTodos"
                :set-current-todo="setCurrentTodo"
                :save-todos="saveTodos"
            />
        </Transition>
    </div>
    <TodoModal
        v-if="currentTodo !== null"
        modalId="editTodoModal"
        :is-new="false"
        :todo="currentTodo"
        :delete-todo="deleteTodo"
        :save-todos="saveTodos"
    />
    <FixedBottom>
        <Transition>
            <input
                v-show="showInput"
                ref="todoInput"
                list="itemOptions"
                style="height: 45px;"
                type="text"
                class="form-control bg-secondary-subtle"
                placeholder="Add a new item..."
                v-model.trim="newTodo"
                @keyup.enter="addTodo"
                @focusout="showInput=false"
            />
        </Transition>
        <datalist id="itemOptions">
            <option v-for="(name, index) in uniqueItems" :key="index" :value="name" >
            </option>
        </datalist>
        <button v-show="!showInput" @click="showInput = !showInput" style="height: 50px;width: 50px;" class="btn btn-primary rounded-circle fs-5 shadow" type="button"><i class="bi bi-plus-lg"></i></button>
    </FixedBottom>
</template>

<script setup>
// IMPORTS
// -------
import { ref, computed, onMounted, watch, nextTick, useTemplateRef } from 'vue';
import TodoItem from './TodoItem.vue';
import TodoModal from './TodoModal.vue';
import FixedBottom from './FixedBottom.vue';
import {capitalizeString} from '@/Tools';

// REFS
// ----
const todos = ref([]);
const showCompleted = ref(false);
const showInput = ref(false);
const currentTodo = ref({name:"",done:false,id:0});
const newTodo = ref("");
const todoInput = useTemplateRef("todoInput");
const showIncompleteButton = useTemplateRef("showIncompleteButton");


// REGULAR METHODS
// ---------------
const loadTodos = () => {
  todos.value = JSON.parse(localStorage.getItem('todoItems')) || [];
};

const removeFocus = (event) => {
    setTimeout(()=>{
        event.target.style.outline = 'none';
        event.target.style.boxShadow = 'none';
        showIncompleteButton.value.blur();
    },250)
}

const toggleShowCompleted = (event) => {
    showCompleted.value = !showCompleted.value;
    removeFocus(event);
}

const addTodo = () => {
    if (newTodo.value) {
        todos.value.push({ id: Date.now(), name: capitalizeString(newTodo.value), done: false });
        newTodo.value = '';
        saveTodos();
    }
}

const setCurrentTodo = (todo) => {
    currentTodo.value = todo;
}

const deleteTodo = (id) => {
    todos.value = todos.value.filter(todo => todo.id !== id );
    saveTodos();
}

const saveTodos = () => {
    localStorage.setItem('todoItems', JSON.stringify(todos.value));
}

// COMPUTED METHODS
// ----------------
const sortedTodos = computed(()=>{
    return [...todos.value].sort((a,b)=>b.id - a.id);
})

const completedTodos = computed(()=>{
    return sortedTodos.value.filter(todo => todo.done);
})

const incompleteTodos = computed(()=>{
    return sortedTodos.value.filter(todo => !todo.done);
})

const completedCount = computed(()=>{
    return completedTodos.value.length;
})

const uniqueItems = computed(()=>{
    return [...new Set(todos.value.map(todo => todo.name))].sort()
})

const chevronClass = computed(()=>{
    return showCompleted.value ? "bi bi-chevron-down" : "bi bi-chevron-right";
})

// WATCHERS AND EVENTS
// -------------------
// Focus input element when it becomes visible
watch(
    () => showInput.value,
    (newValue) => {
        if(newValue){
            nextTick(()=>{
                todoInput.value.focus();
            })
        }
    }
)
onMounted(loadTodos);

</script>

<style scoped>
.btn {
  z-index: 1;
}
.v-enter-active {
    transition: opacity 0.5s ease;
}
.v-leave-active {
    transition: opacity 0.5s ease;
}
.v-enter-from {
    opacity: 0;
}
.v-leave-to {
    opacity: 0;
}
.container {
    overflow-x: hidden;
}
#showIncompleteButton {
    height: 3.5em;
    border-color: none !important;
    border-style: none !important;
}
#showIncompleteButton:hover {
    filter: brightness(70%);
}
#showIncompleteButton:active{
    background-color: var(--bs-dark-bg-subtle) !important;
    -webkit-transition: background-color 500ms cubic-bezier(0.25, 0.1, 0.25, 1);
    -ms-transition: background-color 500ms cubic-bezier(0.25, 0.1, 0.25, 1);
    transition: background-color 500ms cubic-bezier(0.25, 0.1, 0.25, 1);
}
</style>
