<template>
    <TopNav title="Notes" iconClass="bi bi-journals">
        <template #nav-buttons>
            <button v-if="showEditor" @click="togglePinNote" class="btn py-0 my-0 px-1 mx-1" type="button">
                <i v-if="currentNote.pinned" class="bi bi-pin-fill fs-4 text-warning"></i>
                <i v-else class="bi bi-pin fs-4"></i>
            </button>
            <button v-if="showEditor" data-bs-toggle="modal" data-bs-target="#selectColorModal" class="btn py-0 my-0 px-1 mx-1" type="button">
                <i class="bi bi-palette-fill fs-4 text-primary"></i>
            </button>
            <button v-if="showEditor" data-bs-toggle="modal" data-bs-target="#confirmDeleteModal" class="btn py-0 my-0 px-1 mx-1" type="button">
                <i class="bi bi-trash3 fs-4 text-danger"></i>
            </button>
        </template>
    </TopNav>
    <NoteEditor v-if="showEditor"
        v-model="currentNote"
        :is-new="newNote"
        @hide-editor="hideEditor" 
        @update="createOrUpdateNote"
        @delete="deleteNote" />
    <div v-if="!showEditor" class="container note-list">
        <p v-if="notes.length===0" class="fs-5 text-muted">I know I'm empty right now but I'm full of potential...</p>
        <div class="row g-2">
            <div v-for="(note) in sortedNotes" :key="note.id" class="col-6 list-group list-group-hover no-select d-flex" @click="selectNote(note)" >
                <div class="item list-group-item flex-fill" :class="{'li-active' : currentNote.id===note.id}" :style="{borderColor:note.color}">

                    <div class="d-flex align-items-center flex-wrap mb-1">
                        <div class="fs-6 fw-medium flex-fill">{{ note.title }}</div>
                        <div>
                            <i v-if="note.pinned" class="me-2 bi bi-pin-fill text-warning"></i>    
                            <div class="badge"
                                :style="{backgroundColor:noteColor(note).bg, color:noteColor(note).txt}">
                                {{ new Date(note.id).toLocaleDateString(getUserLocale()) }}
                            </div>
                        </div>
                    </div>
                    <div class="text-muted note-preview">{{ notePreview(note) }}</div>
                </div>
            </div>
        </div>
    </div>
    <FixedBottom v-if="!showEditor">
        <button @click="startNewNote" style="height: 50px;width: 50px;" class="btn btn-primary rounded-circle fs-5 shadow" type="button">
            <i class="bi bi-plus-lg"></i>
        </button>
    </FixedBottom>
</template>

<script setup>
import { ref, onMounted, computed, watch } from 'vue';
import NoteEditor from './NoteEditor.vue';
import FixedBottom from './FixedBottom.vue';
import TopNav from './TopNav.vue';
import {blendColors, hexToRgb, colorOptions, getUserLocale} from '@/Tools.js'
const notes = ref([]);
const showEditor = ref(false);
const newNote = ref(false);
const currentNote = ref();

function hideEditor(){
    showEditor.value = false;
}

const loadNotes = () => {
  notes.value = JSON.parse(localStorage.getItem('notes')) || [];
  currentNote.value = {};
};

const sortedNotes = computed(() => {
  return [...notes.value].sort((a,b) => {
    if (a.pinned === b.pinned) {
        return b.id - a.id; // if not pinned, sort by date descending
    }
    return (a.pinned === true) ? -1 : 1; // else sort by pinned first
  })
});

const notePreview = (note) => {
    let p = note.text.slice(0,100);
    p += p.length < note.text.length ? "..." : "";
    return p;
}

const noteColor = (note)=>{
    let base = note.color || colorOptions.cyan;
    let baseInfo = hexToRgb(base);
    let mix = baseInfo.isLight ? "#000000" : "#FFFFFF";

    return {
        bg:base,
        txt:blendColors(base,mix,0.7)
    }
}

const selectNote = (note) => {
    currentNote.value = note;
    newNote.value = false;
    setTimeout(() => {
      showEditor.value = true;
    }, 100);
}

const saveNotes = () => {
    localStorage.setItem('notes', JSON.stringify(notes.value));
}

const createOrUpdateNote = (updatedNote) => {
  const index = notes.value.findIndex(note => note.id === updatedNote.id);
  updatedNote.modifiedOn = Date.now();
  if (index !== -1) {    
    notes.value[index] = updatedNote;
  } else if (newNote.value) {
    notes.value.push(updatedNote);
  }
  saveNotes();
};

const deleteNote = (id) => {
  notes.value = notes.value.filter(note => note.id !== id);
  saveNotes();
};

const startNewNote = () => {
    currentNote.value = {id:Date.now(), title:"",text:""};
    showEditor.value = true;
    newNote.value = true;
}

const togglePinNote = () => {
    if (currentNote.value.pinned === undefined) {
        currentNote.value.pinned = true;
    } else {
        currentNote.value.pinned = !currentNote.value.pinned;
    }
    saveNotes();
}

watch(() => showEditor.value,
    (newValue) => {
        if(!newValue){
            currentNote.value = {};
        }
    }
)
onMounted(loadNotes);
</script>
<style>
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes slideIn {
    from {
        transform: translateY(20px);
    }
    to {
        transform: translateY(0);
    }
}
.note-viewer {
    animation: fadeIn 500ms ease forwards;
}
.note-editor {
    animation: fadeIn 500ms ease forwards;
}
.note-list {
    overflow-x: hidden;
    animation: fadeIn 500ms ease forwards;
    animation: slideIn 500ms ease forwards;
}
.note-preview {
  font-family: "Fira Code", serif;
  font-size: 0.9em !important;
}
.bi {
    animation: fadeIn 1000ms ease;
}
.btn {
    overflow-y: hidden;
}
</style>
<style scoped>
.list-group-item,
.list-group-item * {
    cursor: pointer;
}
</style>