    }
    .token.title > .token.punctuation {
        color: color-mix(in srgb, var(--bs-red) 70%, var(--bs-light) 30%);
    }
    .list.punctuation {
        color: var(--bs-orange);
    }
    .token.blockquote {
        color: var(--bs-secondary);
    }
    .token.hr {
        color: var(--bs-secondary);
    }
    .token.bold {
        font-weight: bold;
        color: var(--bs-orange);
    }
    .token.bold > .token.punctuation {
        color: color-mix(in srgb, var(--bs-orange) 70%, var(--bs-light) 30%);
    }
    .token.italic {
        font-style: italic;
        color: var(--bs-teal);
    }
    .token.italic > .token.punctuation {
        color: color-mix(in srgb, var(--bs-teal) 70%, var(--bs-light) 30%);
    }
    .token.operator {
        color: var(--bs-orange);
    }
    .token.url > .token.url {
        color: var(--bs-teal);
        text-decoration: underline;
    }
    .token.url > .token.content {
        color: var(--bs-blue)
    }
    .token.table-header {
        font-weight: bold;
    }
    .token.strike > .token.content {
        text-decoration: line-through;
    }
</style>