<template>
    <BaseModal :modal-id="modalId">
        <template #header>
            <h5 v-if="isNew" class="modal-title">Add a new item...</h5>
            <h5 v-else class="modal-title">Edit item</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal"/>
        </template>
        <template #body>
            <div class="d-flex align-items-center">
                <input v-show="!isNew" type="checkbox"
                        class="form-check-input me-2 rounded-circle"
                        v-model="todoCopy.done"
                        @change="saveTodos"
                />
                <input v-if="!isNew" type="text"
                    class="form-control"
                    v-model.trim="todoCopy.name"
                    @change="saveTodos"
                />
                <input v-else type="text"
                    class="form-control"
                    v-model.trim="todoCopy.name"
                    @keyup.enter="addNewTodo(todoCopy)"
                />
            </div>
        </template>
        <template #footer>
            <button v-if="isNew" @click="addNewTodo(todoCopy)" type="button" class="btn btn-primary" data-bs-dismiss="modal">
                <i class="bi bi-file-arrow-up-fill"></i>
                Save
            </button>
            <button v-else @click="deleteTodo(todo.id)" type="button" class="btn btn-danger" data-bs-dismiss="modal">
                <i class="bi bi-trash3"></i>
                Delete
            </button>
        </template>
    </BaseModal>
</template>

<script>
import BaseModal from './BaseModal.vue';
export default {
    components:{
        BaseModal
    },
    props: {
        modalId: {
            type: String,
            required: true
        },
        isNew: {
            type: Boolean,
            required: true
        },
        todo: {
            type: Object,
            required: true
        },
        deleteTodo: {
            type: Function,
            required: false
        },
        addNewTodo: {
            type: Function,
            required: false
        },
        saveTodos: {
            type: Function,
            required: false
        }
    },
    data() {
        return {
            todoCopy: this.isNew ? {"name":"","done":false,"id":Date.now()} : this.todo
        };
    },
    watch: {
        todo(val){
            this.todoCopy = this.isNew ? {"name":"","done":false,"id":Date.now()} : val;
        }
    }
};
</script>
